body{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
}
.container{
  width: 100%;
  height: 100%;
  display: grid;
  grid-template-columns:max-content;
  column-gap: 1rem;
  margin: 1rem;
}
.left{
  width:100%;
}
.left>img{
  width: 500px;
  text-align: center;
}
.right{
  padding:2rem;
}
input{
  outline: none;
  border: none;
  border-radius: 10px;
  background-color:#E9F1FA;
  width: 50%;
  height: 2rem;
  font-size: 1.2rem;
  font-weight: 500;
  text-indent: .5rem;
}
h3{
  color: green;
}

@media screen and (max-width:575px) {
  .right{
     padding: 0 2rem;
  }
  .right>h1{
     font-size: 28px;
  }
  .right>input{
     width: 40%;
  }

}



